/* Estilos base para login alinhados ao cadastro */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  z-index: 1000;
  justify-content: center;
  align-items: center;
}

.loginContainer {
  background-color: white;
  width: 100%;
  max-width: 400px;
  padding: 2rem;
  overflow-y: auto;
  transition: transform 0.3s ease-in-out;
  height: auto;
  border-radius: 12px;
}

.loginHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.closeButton {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.5rem;
}

.closeButton:hover {
  color: #666;
}

.title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  margin: 0;
  text-align: center;
}

.loginForm {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.formGroup label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #444;
}

.formGroup input {
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  transition: all 0.2s;
}

.formGroup input:focus {
  outline: none;
  border-color: black;
  box-shadow: 0 0 0 2px rgba(0, 102, 204, 0.2);
}

.errorText {
  color: #dc2626;
  font-size: 0.75rem;
}

.submitButton {
  background-color: black;
  color: white;
  padding: 0.75rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
  margin-top: 1rem;
}

.submitButton:hover {
  background-color: #494e53;
}

/* Mensagens de status */
.statusMessage {
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 20px;
  text-align: center;
}

.success {
  background-color: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.error {
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

/* Estilos para desktop */
@media (min-width: 768px) {
  .loginContainer {
      width: 400px;
      transform: translateX(-100%);
  }

  .loginContainer.open {
      transform: translateX(0);
  }
}

/* Estilos para dispositivos móveis */
@media (max-width: 767px) {
  .loginContainer {
      width: 90%;
      margin: auto;
      border-radius: 8px;
      max-height: 90vh;
      transform: translateY(100%);
  }

  .loginContainer.open {
      transform: translateY(0);
  }
}
